.radiobox {
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  position: relative;
}
.radiobox input[type=checkbox] {
  width: 0;
  height: 0;
  position: absolute;
  z-index: -1;
  opacity: 0;
  display: block;
}
.radiobox input[type=checkbox]:checked {
  z-index: 1;
  content: "";
}
.radiobox input[type=checkbox]:checked + label::after {
  content: "";
  z-index: 2;
}
.radiobox label {
  position: relative;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
}
.radiobox label::after {
  content: "";
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 4px;
  border: 1px solid #E0E0E0;
  background-color: #ffffff;
}

.checkbox {
  position: relative;
}

.custom-checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

/*   label,   .custom-checkbox */
.custom-checkbox + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  position: relative;
  cursor: pointer;
}

/*   label  before    */
.custom-checkbox + label::before {
  content: "";
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border: 1px solid #E0E0E0;
  border-radius: 4px;
  flex-shrink: 0;
  flex-grow: 0;
  cursor: pointer;
}

.custom-checkbox + label::after {
  content: "";
  width: 10.667px;
  height: 8.23px;
  flex-shrink: 0;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  background-image: url(../img/svg/arrows/check.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

/*   ,    checked */
.custom-checkbox:checked + label::before {
  background-color: #004D43;
  border: 1px solid #004D43;
}

.custom-checkbox:checked + label::after {
  opacity: 1;
}

