.protection-preview {
  &__item {
    padding-top: 144px;
    padding-bottom: 144px;

    &::before {
      opacity: 0.7;
      background: linear-gradient(90deg, #000 0%, rgba(0, 0, 0, 0.00) 100%);
    }
  }

  &__text {
    max-width: 943px;
  }
}

@media screen and (max-width: $laptop) {
  .protection-preview {
    &__item {
      padding-top: 92px;
      padding-bottom: 92px;
      min-height: 364px;
    }
  }
}

@media screen and (max-width: $mob) {
  .protection-preview {
    &__item {
      padding-top: 52px;
      padding-bottom: 52px;
      min-height: 400px;
    }
  }
}

.forestry {
  &__container {
    display: grid;
    grid-template-columns: auto 566px;
    column-gap: 60px;
    margin-bottom: 100px;
  }

  &__title {
    margin-bottom: 40px;
  }

  &__right {
    @include flexFsCenter;
  }

  &__image {
    width: 443px;
    height: 406px;
    flex-shrink: 0;

    img {
      width: 100%;
      height: 100%;
    }
  }
}

.forestry-rational {
  display: grid;
  grid-template-columns: 11fr 13fr;
  overflow: hidden;
  border-radius: 20px;
  background-color: $greyCircle;
  box-shadow: -10px -10px 20px 0px rgba(255, 255, 255, 0.20), 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.50);

  &__image {
    @include imageCoverWidth;
    height: 100%;
  }

  &__right {
    background: linear-gradient(59deg, #004D43 0%, #02B45E 96.87%);
    @include flexCenter;
    padding: 60px;
  }

  &__title {
    color: $white;
    margin-bottom: 24px;
  }

  &__content {
    p {
      color: $white;
    }
  }
}

.forestry-complex {
  background: var(--6, linear-gradient(258deg, #FFF 18.23%, #EFF2F7 87.8%));
  -webkit-box-shadow: 10px -10px 20px 0px rgba(170, 170, 204, 0.5) inset, 5px -5px 10px 0px rgba(170, 170, 204, 0.25) inset;
  box-shadow: 10px -10px 20px 0px rgba(170, 170, 204, 0.5) inset, 5px -5px 10px 0px rgba(170, 170, 204, 0.25) inset;
  background-image: url(../img/jpg/backgr/bg.jpg);
  position: relative;
  @include bgcover;

  &__container {
    @include gridColumn(2);
    align-items: flex-start;
  }

  &__title {
    color: $green;
    margin-bottom: 24px;
  }

  &__text {
    margin-bottom: 48px;
  }

  &__image {
    margin-left: 60px;
    background-color: $white;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: -10px -10px 20px 0px rgba(255, 255, 255, 0.20), 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.50);
    padding: 12px 16px;

    &-inner {
      @include imageCoverWidth;
      height: 435px;
    }
  }
}

.forestry-slider {
  background: linear-gradient(180deg, #FFF 0%, #EFF2F7 99.96%);

  &__swiper {}

  &__item {
    overflow: hidden;
    border-radius: 20px;
    background-color: $white;
    box-shadow: -10px -10px 20px 0px rgba(255, 255, 255, 0.20), 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.20);

    &-image {
      @include imageCoverWidth;
      height: 210px;
    }

    &-content {
      padding: 24px 24px 32px 24px;
      min-height: 304px;
      box-sizing: border-box;
      @include flexColumn;
      justify-content: flex-start;
    }

    &-name {
      @extend %bold20;
      color: $green;
      display: inline-block;
      margin-bottom: 8px;
    }

    &-text {
      @extend %regular16;
      color: $text;
      overflow: hidden;
      height: 144px;
      flex-shrink: 0;
      margin-bottom: 8px;

      p {
        @include mb(12px);
      }

      &.active {
        min-height: 144px;
        height: 100%;
      }
    }

    &-bottom {
      @include flexFeBetween;
      margin-top: auto;

      &.active {
        .forestry-slider__item-arrow {
          background-image: url(../img/svg/arrow-link-green.svg);
        }

        .forestry-slider__item-button {
          .txt1 {
            display: none;
          }

          .txt2 {
            display: block;
          }
        }
      }
    }

    &-button {
      @extend %regular16;
      color: $green;
      border-bottom: 1px dotted $green;
      cursor: pointer;

      .txt2 {
        display: none;
      }
    }

    &-arrow {
      width: 32px;
      height: 32px;
      flex-shrink: 0;
      background-image: url(../img/svg/arrow-link-dark.svg);
      cursor: pointer;
    }
  }
}

.protection-slider {
  background-color: $blue;

  &__row {
    @include mb(100px);
  }

  &__title {
    color: $green;
    margin-bottom: 24px;
  }

  &__slide {
    height: auto;
  }

  &__item {
    overflow: hidden;
    border-radius: 20px;
    background-color: $white;
    height: 100%;
    box-shadow: -10px -10px 20px 0px rgba(255, 255, 255, 0.20), 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.20);
    @include flexColumn;

    &-image {
      @include imageCoverWidth;
      height: 210px;
      flex-shrink: 0;
    }

    &-content {
      padding: 24px 24px 32px 24px;
      height: 100%;
      box-sizing: border-box;

      strong {
        @extend %bold20;
        color: $green;
        display: inline-block;
        margin-bottom: 8px;
      }

      ul {
        &>li {
          @extend %regular16;
          color: $text;
          @include mb(8px);
          padding-left: 25px;
          position: relative;

          &::before {
            content: '';
            position: absolute;
            background-color: $text;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            left: 0;
            top: 9px;
          }
        }
      }
    }
  }
}

.protection-system {
  background: var(--6, linear-gradient(258deg, #FFF 18.23%, #EFF2F7 87.8%));
  -webkit-box-shadow: 10px -10px 20px 0px rgba(170, 170, 204, 0.5) inset, 5px -5px 10px 0px rgba(170, 170, 204, 0.25) inset;
  box-shadow: 10px -10px 20px 0px rgba(170, 170, 204, 0.5) inset, 5px -5px 10px 0px rgba(170, 170, 204, 0.25) inset;
  background-image: url(../img/jpg/backgr/bg.jpg);
  position: relative;
  @include bgcover;

  &__container {
    display: grid;
    grid-template-columns: 7fr 5fr;
    align-items: center;
  }

  &__content {
    margin-right: 60px;
  }

  &__title {
    margin-bottom: 24px;
  }

  &__right {
    border-radius: 20px;
    box-shadow: -10px -10px 20px 0px rgba(255, 255, 255, 0.20), 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.50);
    overflow: hidden;
    padding: 20px;
    box-sizing: border-box;
  }

  &__image {
    @include imageCoverWidth;
    height: 400px;
    flex-shrink: 0;

    img {
      width: 100%;
      height: 100%;
    }
  }
}

.protection-doc {
  background: linear-gradient(180deg, #EFF2F7 17.71%, #FFF 100%);
  box-shadow: 10px -10px 20px 0px rgba(170, 170, 204, 0.50), 5px -5px 10px 0px rgba(170, 170, 204, 0.25);

  &__block {
    border-radius: 20px;
    padding: 64px;
    background-color: $white;
    box-shadow: -10px -10px 20px 0px rgba(255, 255, 255, 0.20), 10px 10px 20px 0px rgba(170, 170, 204, 0.50), 5px 5px 10px 0px rgba(170, 170, 204, 0.25), -5px -5px 10px 0px rgba(255, 255, 255, 0.50);
  }

  &__title {
    margin-bottom: 40px;
    color: $green;
  }

  &__items {
    @include gridColumn(3);
    column-gap: 40px;
    row-gap: 40px;
  }
}